<template>
    <div class="singleBlog">
      <h2>{{blog.title}}</h2>
      <article>{{blog.content}}</article>
      <p>作者：{{blog.author}}</p>
      <p>分类：</p>
      <ul>
        <li v-for="category in blog.categories" :key="category">{{category}}</li>
      </ul>
      <button v-on:click="deleteBlog">删除</button>
      <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'singleBlog',
  data () {
    return {
      id: this.$route.params.id,
      blog: {}
    }
  },
  created () {
    // this.$http.get('https://vuedemo-30ec9.firebaseio.com/posts/'+this.id+'.json')
    axios.get('https://vuedemo-30ec9.firebaseio.com/posts/' + this.id + '.json')
      .then(data => {
        this.blog = data.data
      })
  },
  methods: {
    deleteBlog: function () {
      this.$http.delete('https://vuedemo-30ec9.firebaseio.com/posts/' + this.id + '.json').then(response => {
        this.$router.push({ path: '/' })
      })
    }
  }
}
</script>

<style scoped>
.singleBlog{
  max-width: 960px;
  padding: 20px;
  margin: 0 auto;
  background: #eeeeee;
  border: 1px dotted #aaaaaa;
}
</style>
