<template>
    <div id="show-blog">
      <h1>博客总览</h1>
      <input type="text" placeholder="搜索" v-model="search">
      <div  v-for="blog in filterBlogs" class="single-blog">
        <router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title}}</h2></router-link>
        <article>{{blog.content | sniep}}</article>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'show-blog',
  data () {
    return {
      blogs: [],
      search: ''
    }
  },
  created () {
    // this.$http.get('https://vuedemo-30ec9.firebaseio.com/posts.json')
    axios.get('https://vuedemo-30ec9.firebaseio.com/posts.json')
      .then(function (data) {
        console.log(data)
        return data.data
      }).then(data => {
        // console.log(data);
        let blogsArray = []
        for (let key in data) {
          // console.log(key);
          data[key].id = key
          // console.log(data[key]);
          blogsArray.push(data[key])
        }
        this.blogs = blogsArray
      })
  },
  computed: {
    filterBlogs: function () {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search)
      })
    }
  },
  filters: {
    sniep: function (value) {
      return value.slice(0, 100) + '...'
    }
  }

}
</script>

<style scoped>
#show-blog{
  max-width: 560px;
  margin: 0 auto;
}
  .single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eeeeee;
    border: 1px dotted #aaaaaa;
  }
  #show-blog a{
    color: #444444;
    text-decoration: none;
  }
  input[type='text']{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
  }

</style>
